//     (   API  )
export function getTechnicalIndicators(symbol) {
  // console.log(`Fetching technical indicators for ${symbol}...`); //  API   
  return {
    ma: Math.random() * 100,
    ema: Math.random() * 100,
    rsi: Math.random() * 100,
    macd: (Math.random() * 2 - 1) * 10,
  };
}

//  :     (3  )
export function getWeatherPrediction(symbol) {
  // console.log(`Fetching weather prediction for ${symbol}...`); //  API   
  const weathers = [
    { icon: "", label: "", description: " " },
    { icon: "", label: "", description: " " },
    { icon: "", label: "", description: " " },
    { icon: "", label: "", description: " " },
    { icon: "", label: "", description: " " },
    { icon: "", label: "", description: " " },
    { icon: "", label: "", description: " " },
  ];

  const dailyForecasts = ["yesterday", "today", "tomorrow"].map((dayType) => {
    const idx = Math.floor(Math.random() * weathers.length);
    const weather = weathers[idx];
    return {
      day: dayType,
      icon: weather.icon,
      label: weather.label,
      description: weather.description,
      tooltip: `${symbol} ${dayType === "today" ? "" : dayType === "yesterday" ? "" : ""} : ${weather.description}`,
    };
  });

  return dailyForecasts;
}

//     (7   )
export function getMarketCapHistory(symbol) {
  // console.log(`Fetching market cap history for ${symbol}...`); //  API   
  const labels = [];
  const data = [];
  for (let i = 6; i >= 0; i--) {
    const d = new Date();
    d.setDate(d.getDate() - i);
    labels.push(`${d.getMonth() + 1}/${d.getDate()}`);
    //          ()
    let baseValue = 100;
    if (symbol === "BTC") baseValue = 500;
    else if (symbol === "ETH") baseValue = 300;
    data.push(baseValue + Math.random() * (baseValue / 2));
  }
  return { labels, data };
}

//  /  (0 ~ 100   )
export function getFearGreedIndex() {
  return Math.floor(Math.random() * 101);
}

// /     
export function getFearGreedLabel(value) {
  if (value < 20) return " ";
  if (value < 40) return "";
  if (value < 60) return "";
  if (value < 80) return "";
  return " ";
}

// /     
export function getFearGreedEmoji(value) {
  if (value < 20) return "";
  if (value < 40) return "";
  if (value < 60) return "";
  if (value < 80) return "";
  return ""; // output5.txt  ""  . (  "" )
}

